body {
    padding: 0;
    margin: 0;
    background-color: $mainBackgroundColor;
}

.app {
    background-color: $mainBackgroundColor;
    margin: 0;
    padding: 0;
}

// Link
/* unvisited link */
a:link {
    color: $fontMainColor;
    text-decoration: none;
  }
  
  /* visited link */
  a:visited {
    color: $fontMainColor;
  }
  
  /* mouse over link */
  a:hover {
    color: $fontMainColor;
  }
  
  /* selected link */
  a:active {
    color: $fontMainColor;
  }
